#allDisplay{
  position: relative;

    width: 71vw;
    height: 40vh;
    margin-top: 2%;
    margin-left: 25vw;

    display: flex;
    /* flex-direction: row;
    column-gap: 10%;
    row-gap: 5%; */

    justify-content: center;
    align-items: center;

   /*  flex-wrap: wrap; */

    
    overflow-y: scroll;


    background-color: #FFFFFF;
    border: 4px solid #02BA90;
    
